import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

document.documentElement.style = 'margin: 0; padding: 0; height: 100%; width: 100%';
document.body.style = 'margin: 0; padding: 0; height: 100%; width: 100%';

function ajaxCall(method, address, params, readyStateAction) {
    var xhr = new XMLHttpRequest();
    xhr.open(method, address, true);
    xhr.onreadystatechange = function() {
        if (this.readyState !== 4) return;
        if (this.status !== 200) {
            console.log('response geeft errorcode' + this.status);
            return;
        }
        readyStateAction(this.responseText);
    };
    xhr.send(params);
}

